import { Menu, Modal } from 'antd'
import { Export,Edit, History } from 'assets/svg/icon'
import EllipsisDropdown from 'components/shared-components/EllipsisDropdown'
import CustomIcon from 'components/util-components/CustomIcon'
import React, { useEffect } from 'react'
import { useState } from 'react'
import { Link } from 'react-router-dom'
import './Events.css'
import { DeleteOutlined, EyeOutlined } from '@ant-design/icons'
import { membershipEventBooking } from '../data'
import Helper from '../Helper'
import axios from 'axios'



export default function Events() {

  const [eventsData, setEventsData] = useState([])
  

  const onDeleteData = (record,dataSet,Id) => {
    // console.log(Id)
    // console.log(record)
    Modal.confirm({
      title: "Are you sure, you want to delete this members record ?",
      okText: "Yes",
      okType: "danger",
      onOk: () => {
        deleteEvents(record, dataSet, Id)
      }
    })
  }

    const membershipBookingColumns = [
        {
          title: 'Event Id',
          dataIndex: 'id',
        },
        {
          title: "Event Name",
          dataIndex: 'name',
          render : text =>{
            return <p className='font-weight-bold text-dark' >{text.length>18 ? `${text.slice(0,18)}...` : text}</p>
          }
        },
        {
          title: "Event Date",
          // dataIndex: 'event_date',
          render : (record) =>{
            return (
              <div>{new Date(record.date_time).toLocaleDateString()}</div>
            )
          }
        },
        {
          title: "Event Time",
          // dataIndex: 'event_time',
          render : (record) =>{
            return (
              <div>{new Date(record.date_time).toLocaleTimeString()}</div>
            )
          }
        },
        {
          title: "Event Address",
          dataIndex: 'address',
        },
        {
          title: "Event Fee/Pax",
          dataIndex: 'fee_for_member',
        },
        {
          title: "Status",
          dataIndex: 'status',
          render: text => {
            return <p className={`${text === "Cancel" ? 'text-danger membershipPaymentPending' : "text-success membershipPaymentPaid"} font-weight-semibold`}>{text}</p>
          }
        },
        {
          title: "Action",
          // dataIndex: 'action',
          render: (record) => {
            return (
              <>
                <EllipsisDropdown menu={
                  <Menu>
                    <Menu.Item>
                      <Link to='/app/events' > <EyeOutlined className='mr-2 ' />View Details</Link >
                    </Menu.Item>
                    <Menu.Item>
                      <span onClick={() => onDeleteData(record,setEventsData,'id')}> <DeleteOutlined className='mr-2 ' />Delete</span>
                    </Menu.Item>
                    <Menu.Item>
                  <Link to={`event_list/update/${record.id}`} className='d-flex align-items-center' ><CustomIcon className='mr-2' svg={Edit} />Edit</Link>
                </Menu.Item>
                  </Menu>
                } />
    
              </>
            );
          },
        },
      ]

      const getEvents = () =>{
        axios.get("http://127.0.0.1:3333/events").then((response) => {
          console.log(response)  
          if (response.data.status) {
            setEventsData(response.data.result);        
          }else{
            console.log(response)
          }
        });
      }

      const deleteEvents = (record, dataSet, Id) => {
        axios.delete("http://127.0.0.1:3333/events/delete", { data: { id: record[Id] } }).then((response) => {
          console.log(response)
          if (response.data.status) {
            dataSet((pre) => {
              return pre.filter((member) => member[Id] !== record[Id])
            })
          }
          alert(response.data.status)
        }).catch((err) => {
          console.log(err)
        });
      }
    
      useEffect(() => {
        getEvents();
      }, [])

  return (
    <div>
      <div className='d-flex justify-content-between mb-3'>
        <div className='membershipPlanTableSearchFilter'>

        <form className='memberDetailSearch'>
          <CustomIcon svg={History} /> <input className='memberDetailSerachInput' placeholder='Search' type="text" name="search" id="" />
        </form>
        <div className='memberDetailFilter'>
          <CustomIcon svg={History} /> <span className='memberDetailFilterText'> Filters</span>
        </div>
        <div className='memberDetailFilter'>
          <CustomIcon svg={Export} /> <span className='memberDetailFilterText'> Export</span>
        </div>
        </div>
        <Link to='event_list/add_new' className='bg-info d-flex align-items-center rounded text-white font-weight-semibold px-4'>Add New</Link>
      </div>
      <div>
      <Helper clients={eventsData} attribiue={membershipBookingColumns} />
      </div>

    </div>
  )
}
